.personal-project-body {
  width: 100vw;
  height: 80vh;
  font-family: "Work Sans", sans-serif;
  font-weight: 400;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  /* background-color: white; */
  border: 1 solid black;
}

.personal-project-title {
  font-size: 2rem;
  font-weight: 600;
  /* padding: 24px; */
  /* margin: 48px 0 0 50vw; */
  /* margin-left: 50vw; */
  text-transform: uppercase;
  /* border-top: .5px solid black; */
  /* border-bottom: .5px solid black; */
  overflow: hidden;
}

.personal-project-middle {
  display: flex;
  flex-grow: 1;
}

.personal-project-img-container {
  box-sizing: border-box;
  width: 50vw;
  display: flex;
  flex-wrap: wrap;
  /* align-items: center; */
  gap: 24px;
  padding: 24px;
  box-sizing: border-box;
}

.personal-project-img {
  object-fit: contain;
  height: 200px;
  /* transition: .5s ease; */
}

/* .personal-project-img:hover {
  height: 500px;
  transition: .5s ease;
} */

.personal-project-text {
  box-sizing: border-box;
  width: 50vw;
  padding: 24px;
  font-size: 1rem;
  line-height: 2rem;
  position: sticky;
  top: 0;
}

/* .personal-project-paragraph {
  position: sticky;
  top: 0;
} */

.personal-project-footnote {
  display: flex;
  justify-content: space-between;
  border-top: .5px solid black;
  width: calc(100vw - 48px);
  padding: 24px;
  justify-self: flex-end;
}

.personal-project-link {
  opacity: 0.5;
  font-size: 1rem;
}

.personal-project-link:hover {
  opacity: 0.9;
  cursor: alias;
  transition: 0.5s ease;
}
